<template>
    <canvas ref="vgraph" />
</template>
<script>
import {Graph} from 'vivagraphjs'

export default {
    name: 'v-graph',
    props: {
        nodes: Array,
        links: Array,
    },
    methods: {
        show() {
            this.renderer.run();
        }
    },
    mounted() {
        this.show();
    },
    computed: {
        canvas() {
            return this.$refs.vgraph;
        },
        graph() {
            let g = Graph.graph();
            this.nodes.forEach(function() {
                g.addNode.apply(g, arguments); 
            });
            this.links.forEach(function() {
                g.addLink.apply(g, arguments);
            });
            return g;
        },
        hasWebGLSupport() {
          return window.WebGLRenderingContext
            && this.canvas.getContext 
            && this.canvas.getContext('experimental-webgl');
        },
        layout() {
            return Graph.Layout.forceDirected(this.graph, {
                springLength : 50,
                springCoeff : 1e-4,
                dragCoeff : 0.005,
                gravity : -1.5,
                theta : 0.75,
            });
        },
        graphics() {
            return this.hasWebGLSupport 
                ? Graph.graphics.View.webglGraphics() 
                : Graph.graphics.View.svgGraphics();
        },
        renderer() {
            return Graph.View.renderer(this.graph, {
                layout: this.layout,
                graphics: this.graphics,
                renderLinks: true,
                container: this.canvas,
            });
        }
    },
    data() {
        return {
            // graph: Graph.graph(),
        }
    }
}
</script>